<template>
  <el-container class="container">
    <el-main>
      <h1 class="home-title">Property Description</h1>
      <div style="display: flex;flex-direction: row-reverse;">
        <el-button type="primary" @click="dialogVisible = true" style="margin-right: 30px;margin-bottom: 10px;">UPLOAD</el-button>
      </div>
      <el-dialog title="UPLOAD CONFIGURATION" :visible.sync="dialogVisible">
        <el-form ref="form" :model="form" :rules="rules">
          <el-form-item label="user name" prop="user">
            <el-input v-model="form.user" placeholder="user name"></el-input>
          </el-form-item>
          <el-form-item label="rule name" prop="rule">
            <el-select v-model="form.rule" placeholder="rule name" filterable>
              <el-option v-for="item in appOptions" :key="item.value" :label="item.label" :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <span slot="footer">
          <el-button @click="dialogVisible = false">Cancel</el-button>
          <el-button type="primary" @click="upload">Confirm</el-button>
        </span>
      </el-dialog>
      <!-- list 配置结果-->
      <div v-for="(t, i) in propertyList" :key="i">
        <el-card class="card-margin">
          <el-descriptions :title="'Property ' + (i + 1)" :column="1" border>
            <template slot="extra">
              <el-button size="mini" icon="el-icon-edit" @click="updateItem(t, i)">
              </el-button>
              <el-button size="mini" icon="el-icon-delete" @click="removeItem(i)"></el-button>
            </template>
            <el-descriptions-item label="Abstract Level">{{ t.template }}</el-descriptions-item>
            <el-descriptions-item label="value">{{ t.description }}</el-descriptions-item>
            <!-- <el-descriptions-item label="ltl" v-if="t.ltl">{{ t.ltl }}</el-descriptions-item> -->
          </el-descriptions>
        </el-card>
      </div>
      <!-- 添加按钮和配置对话框 -->
      <el-card style="display: flex;align-content: center;justify-content: center;" class="card-margin">
        <el-button type="text" @click="addItem()">
          <i class="el-icon-circle-plus" style="font-size: 25px;"> Add Property</i>
        </el-button>
        <el-dialog title="Template" :visible.sync="templateDialog" @close="cancelAddItem">
          <el-form ref="template" :model="template" label-width="20%" :rules="template_rule">
            <el-form-item label="Category" prop="category">
              <el-select @change="categoryChange" v-model="template.category" placeholder="Please Choose the Template Type">
                <el-option v-for="item in categoryOptions" :key="item.value" :label="item.label" :value="item.value" :title="item.description">
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="Template" prop="template">
              <el-select v-model="template.template" placeholder="Please Choose the Template">
                <el-option v-for="item in templateOptions" :key="item.value" :label="item.label" :value="item.value" :title="item.description">
                </el-option>
              </el-select>
            </el-form-item>
          </el-form>
          <div slot="footer">
            <el-button @click="cancelAddItem">Cancel</el-button>
            <el-button type="primary" @click="confimAddItem">Confirm</el-button>
          </div>
        </el-dialog>
        <!-- <EffectEffectTime ref="EffectEffectTimeComponent"></EffectEffectTime> -->
        <EffectTime ref="EffectTimeComponent"></EffectTime>
        <EventStateTime ref="EventStateTimeComponent"></EventStateTime>
        <StateTime ref="StateTimeComponent"></StateTime>
        <!-- <EventTime ref="EventTimeComponent"></EventTime> -->
        <!-- <EventEffectTime ref="EventEffectTimeComponent"></EventEffectTime> -->
      </el-card>
    </el-main>
  </el-container>
</template>

<script>
import { appOptions, categoryOptions, tempTemplateOptions, symbolOptions } from '../../public/global/data.js'
// import EffectEffectTime from '../components/EffectEffectTime.vue'
import EffectTime from '../components/EffectTime.vue'
import EventStateTime from '../components/EventStateTime.vue'
import StateTime from '../components/StateTime.vue'
// import EventTime from '../components/EventTime.vue'
import axios from 'axios'

export default {
  name: 'HomeView',
  components: {
    // EffectEffectTime,
    EffectTime,
    EventStateTime,
    StateTime
    // EventTime,
    // EventEffectTime
  },
  data () {
    return {
      beginFlag: false,
      propertyList: this.PROPERTY.property,
      form: {
        user: '',
        rule: ''
      },
      rules: {
        user: [
          { required: true, message: 'please input your name.', trigger: 'blur' }
        ],
        rule: [
          { required: true, message: 'Please select the application name.', trigger: 'blur' }
        ]
      },
      template: {
        category: '',
        template: ''
      },
      template_rule: {
        category: [
          { required: true, message: 'please select the category.', trigger: 'change' }
        ],
        template: [
          { required: true, message: 'please select the template.', trigger: 'blur' }
        ]
      },
      dialogVisible: false,
      templateDialog: false,
      appOptions,
      categoryOptions,
      tempTemplateOptions,
      symbolOptions,
      templateOptions: []
    }
  },
  methods: {
    // 修改property
    updateItem (Info, index) {
      if (Info && index >= 0) {
        const item = JSON.parse(JSON.stringify(Info))
        let temp = ''
        switch (item.template) {
          case 'effect-effect':
            this.$router.push({
              name: 'EffectEffect',
              params: {
                update: index,
                value: item.value
              }
            })
            break
          // case 'effect-effect-time':
          //   this.$refs.EffectEffectTimeComponent.EffectEffectTimeDialog = true
          //   this.$refs.EffectEffectTimeComponent.updateFlag = true
          //   this.$refs.EffectEffectTimeComponent.index = index
          //   temp = item.value.split(' & ')
          //   temp[0] = temp[0].split('.')
          //   temp[1] = temp[1].split('.')
          //   this.$refs.EffectEffectTimeComponent.LocationOneChange(temp[0][0])
          //   this.$refs.EffectEffectTimeComponent.LocationTwoChange(temp[1][0])
          //   this.$refs.EffectEffectTimeComponent.EffectEffectTime = {
          //     location_one: temp[0][0],
          //     effect_one: temp[0][1],
          //     location_two: temp[1][0],
          //     effect_two: temp[1][1],
          //     symbol: temp[2],
          //     interval: parseInt(temp[3]),
          //     intervalUnit: (/[^0-9]+/g.exec(temp[3]))[0]
          //   }
          //   break
          case 'effect-time':
            this.$refs.EffectTimeComponent.EffectTimeDialog = true
            this.$refs.EffectTimeComponent.updateFlag = true
            this.$refs.EffectTimeComponent.index = index
            temp = item.value.split(' & ')
            temp[0] = temp[0].split('.')
            this.$refs.EffectTimeComponent.LocationChange(temp[0][0])
            this.$refs.EffectTimeComponent.EffectTime = {
              location: temp[0][0],
              effect: temp[0][1],
              symbol: temp[1],
              time: parseInt(temp[2]),
              timeUnit: (/[^0-9]+/g.exec(temp[2]))[0]
            }
            break
          case 'state-state':
            this.$router.push({
              name: 'StateState',
              params: {
                update: index,
                value: item.value
              }
            })
            break
          case 'event-state':
            this.$router.push({
              name: 'EventState',
              params: {
                update: index,
                value: item.value
              }
            })
            break
          case 'event-state-time':
            this.$refs.EventStateTimeComponent.EventStateTimeDialog = true
            this.$refs.EventStateTimeComponent.updateFlag = true
            this.$refs.EventStateTimeComponent.index = index
            temp = item.value.split(' & ')
            temp[0] = temp[0].split('.')
            temp[1] = temp[1].split('.')
            this.$refs.EventStateTimeComponent.radio = temp[3]
            this.$refs.EventStateTimeComponent.EventStateTime = {
              location: temp[0][0],
              type: temp[0][1],
              event: temp[0][2],
              location_two: temp[1][0],
              category: temp[1][1],
              state: temp[1][2],
              value: temp[1][3],
              interval: parseInt(temp[2]),
              intervalUnit: (/[^0-9]+/g.exec(temp[2]))[0]
            }
            break
          case 'one-state':
            this.$refs.StateTimeComponent.StateTimeDialog = true
            this.$refs.StateTimeComponent.updateFlag = true
            this.$refs.StateTimeComponent.index = index
            temp = item.value.split(' & ')
            temp[0] = temp[0].split('.')
            this.$refs.StateTimeComponent.StateTime = {
              location: temp[0][0],
              category: temp[0][1],
              state: temp[0][2],
              value: temp[0][3],
              symbol: temp[1],
              time: parseInt(temp[2]),
              timeUnit: (/[^0-9]+/g.exec(temp[2]))[0]
            }
            this.$refs.StateTimeComponent.LocationChange(temp[0][0])
            this.$refs.StateTimeComponent.stateChange(temp[0][2])
            this.$refs.StateTimeComponent.StateTime = {
              location: temp[0][0],
              category: temp[0][1],
              state: temp[0][2],
              value: temp[0][3],
              symbol: temp[1],
              time: parseInt(temp[2]),
              timeUnit: (/[^0-9]+/g.exec(temp[2]))[0]
            }
            break
          // case 'one-event':
          //   this.$refs.EventTimeComponent.EventTimeDialog = true
          //   this.$refs.EventTimeComponent.updateFlag = true
          //   this.$refs.EventTimeComponent.index = index
          //   temp = item.value.split(' & ')
          //   temp[0] = temp[0].split('.')
          //   this.$refs.EventTimeComponent.EventTime = {
          //     location: temp[0][0],
          //     event: temp[0][1],
          //     count: temp[2],
          //     time: parseInt(temp[1]),
          //     timeUnit: (/[^0-9]+/g.exec(temp[1]))[0]
          //   }
          //   break
          case 'effect-state':
            this.$router.push({
              name: 'EffectState',
              params: {
                update: index,
                value: item.value
              }
            })
            break
          case 'effect-state-time':
            this.$router.push({
              name: 'EffectStateTime',
              params: {
                update: index,
                value: item.value
              }
            })
            break
          // case 'event-effect-time':
          //   this.$refs.EventEffectTimeComponent.EventEffectTimeDialog = true
          //   this.$refs.EventEffectTimeComponent.updateFlag = true
          //   this.$refs.EventEffectTimeComponent.index = index
          //   temp = item.value.split(' & ')
          //   console.log(temp)
          //   temp[0] = temp[0].split('.')
          //   temp[2] = temp[2].split('.')
          //   this.$refs.EventEffectTimeComponent.LocationChange(temp[2][0])
          //   this.$refs.EventEffectTimeComponent.EventEffectTime = {
          //     location_one: temp[0][0],
          //     type: temp[0][1],
          //     event: temp[0][2],
          //     location_two: temp[2][0],
          //     effect: temp[2][1],
          //     time: parseInt(temp[1]),
          //     timeUnit: (/[^0-9]+/g.exec(temp[1]))[0]
          //   }
          //   this.$refs.EventEffectTimeComponent.radio = temp[3]
          //   break
          default:
            break
        }
      }
    },
    removeItem (index) {
      this.propertyList.splice(index, 1)
      this.refresh()
    },
    addItem () {
      if (this.beginFlag === false) {
        this.beginFlag = true
        this.TIME.time = new Date().getTime()
      }
      this.templateDialog = true
      this.update = -1
    },
    cancelAddItem () {
      this.templateDialog = false
      this.refresh()
    },
    // 增加property弹窗确认
    confimAddItem () {
      const that = this
      this.$refs.template.validate((valid) => {
        if (valid) {
          this.templateDialog = false
          switch (this.template.template) {
            case 'effect-effect':
              this.$router.push({
                name: 'EffectEffect',
                params: {
                  update: -1,
                  value: ''
                }
              })
              break
            // case 'effect-effect-time':
            //   this.$refs.EffectEffectTimeComponent.EffectEffectTimeDialog = true
            //   this.$refs.EffectEffectTimeComponent.updateFlag = false
            //   this.$refs.EffectEffectTimeComponent.index = -1
            //   break
            case 'effect-time':
              this.$refs.EffectTimeComponent.EffectTimeDialog = true
              this.$refs.EffectTimeComponent.updateFlag = false
              this.$refs.EffectTimeComponent.index = -1
              break
            case 'state-state':
              this.$router.push({
                name: 'StateState',
                params: {
                  update: -1,
                  value: ''
                }
              })
              break
            case 'event-state':
              this.$router.push({
                name: 'EventState',
                params: {
                  update: -1,
                  value: ''
                }
              })
              break
            case 'event-state-time':
              this.$refs.EventStateTimeComponent.EventStateTimeDialog = true
              this.$refs.EventStateTimeComponent.updateFlag = false
              this.$refs.EventStateTimeComponent.index = -1
              break
            case 'one-state':
              this.$refs.StateTimeComponent.StateTimeDialog = true
              this.$refs.StateTimeComponent.updateFlag = false
              this.$refs.StateTimeComponent.index = -1
              break
            // case 'one-event':
            //   this.$refs.EventTimeComponent.EventTimeDialog = true
            //   this.$refs.EventTimeComponent.updateFlag = false
            //   this.$refs.EventTimeComponent.index = -1
            //   break
            case 'effect-state':
              this.$router.push({
                name: 'EffectState',
                params: {
                  update: -1,
                  value: ''
                }
              })
              break
            case 'effect-state-time':
              this.$router.push({
                name: 'EffectStateTime',
                params: {
                  update: -1,
                  value: ''
                }
              })
              break
            // case 'event-effect-time':
            //   this.$refs.EventEffectTimeComponent.EventEffectTimeDialog = true
            //   this.$refs.EventEffectTimeComponent.updateFlag = false
            //   this.$refs.EventEffectTimeComponent.index = -1
            //   break
          }
          Object.keys(this.template).forEach(function (key) {
            that.template[key] = ''
          })
        } else {
          this.$message.warning('Please fill in the form correctly!')
        }
      })
    },
    // 更新界面
    refresh () {
      this.$forceUpdate()
    },
    // 监听模板类别改变
    categoryChange (val) {
      this.template.template = null
      this.templateOptions = this.tempTemplateOptions[val]
    },
    check () {
      // TODO
    },
    // 上传property
    upload () {
      this.beginFlag = false
      const that = this
      if (this.propertyList.length < 1) {
        this.$message.warning('There are no properties to upload!')
        this.dialogVisible = false
        return
      }
      const finalResult = {}
      let ltl = ''
      for (let i = 0; i < this.propertyList.length; i++) {
        finalResult[i] = this.propertyList[i]
        if ('ltl' in this.propertyList[i]) {
          if (ltl === '') {
            ltl = this.propertyList[i].ltl
          } else {
            ltl = ltl + ' | (' + this.propertyList[i].ltl + ')'
          }
        }
      }
      this.$refs.form.validate((valid) => {
        if (valid) {
          that.dialogVisible = false
          axios({
            url: '/upload/saveByTemplate',
            method: 'POST',
            data: {
              user: that.form.user,
              dsl: finalResult,
              time: (new Date().getTime() - that.TIME.time) / 1000,
              rule: that.form.rule
            }
          }).then((response) => {
            that.PROPERTY.property = []
            that.propertyList = []
            that.form = {
              user: '',
              rule: ''
            }
            that.template = {
              category: '',
              template: ''
            }
          })
        }
      })
    }
  },
  mounted () {
  }
}
</script>

<style scoped>
.home-title {
  margin-top: 10px;
  margin-bottom: 10px;
}

.container,
.property {
  height: 100%;
  width: 100%;
}

.card-margin {
  margin: 10px 20px;
}
</style>
