<template>
  <el-container class="state-state-container">
    <el-main>
      <h1 class="home-title">State-State-Condition</h1>
      <div style="display: flex;flex-direction: row-reverse;margin-top: 20px;">
        <el-button type="primary" @click="dialogVisible = true" style="margin-right: 30px;margin-bottom: 10px;">CONFIRM</el-button>
      </div>
      <el-radio v-model="radio" label="always">always</el-radio>
      <el-radio v-model="radio" label="never">never</el-radio>
      <el-alert style="margin-top:10px;margin-bottom:20px;" v-if="radio == 'always'" title="[state] should always be active while [state1, ..., staten]." type="success" :closable="false">
      </el-alert>
      <el-alert style="margin-top:10px;margin-bottom:20px;" v-if="radio == 'never'" title="[state] should never be active while [state1, ..., staten]." type="success" :closable="false">
      </el-alert>
      <el-dialog title="CONFIRM PROPERTY" :visible.sync="dialogVisible" center width="30%">
        <span>Please Confirm the Information</span>
        <span slot="footer">
          <el-button @click="dialogVisible = false">Cancel</el-button>
          <el-button type="primary" @click="Confirm">Confirm</el-button>
        </span>
      </el-dialog>
      <el-row :gutter="30">
        <el-col :span="12">
          <el-card style="display: flex;align-content: center;justify-content: left;" class="card-margin">
            <el-form ref="state" :model="state" label-width="20%" :rules="state_rules">
              <el-form-item label="Location" prop="location">
                <el-select v-model="state.location" placeholder="Please Choose the Location" @change="stateLocationChange">
                  <el-option v-for="item in locationOptions" :key="item.value" :label="item.label" :value="item.value">
                  </el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="category" prop="category">
                <el-select v-model="state.category" placeholder="Please Choose the Category" @change="stateCategoryChange">
                  <el-option label="device" value="device"></el-option>
                  <el-option label="state" value="state"></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="state" prop="state">
                <el-select v-if="state.category=='device'" v-model="state.state" placeholder="Please Choose the Device" style="width: 120px;margin-right:20px" @change="stateChange">
                  <el-option v-for="item in deviceOptions" :key="item.value" :label="item.label" :value="item.value">
                  </el-option>
                </el-select>
                <el-select v-if="state.category=='state'" v-model="state.state" placeholder="Please Choose the State" style="width: 120px;margin-right:20px" @change="stateChange">
                  <el-option v-for="item in stateOptions" :key="item.value" :label="item.label" :value="item.value">
                  </el-option>
                </el-select>
                <el-select v-model="state.value" placeholder="Please Choose the Value">
                  <el-option v-for="item in valueOptions" :key="item.value" :label="item.label" :value="item.value">
                  </el-option>
                </el-select>
              </el-form-item>
            </el-form>
          </el-card>
        </el-col>
        <el-col :span="12">
          <!-- list 配置结果-->
          <div v-for="(t, i) in states" :key="i">
            <el-card class="card-margin">
              <el-descriptions :title="'State ' + (i + 1)" :column="1" border>
                <template slot="extra">
                  <el-button size="mini" icon="el-icon-edit" @click="updateItem(t, i)">
                  </el-button>
                  <el-button size="mini" icon="el-icon-delete" @click="removeItem(i)"></el-button>
                </template>
                <el-descriptions-item label="location">{{ t.location}}</el-descriptions-item>
                <el-descriptions-item label="state">{{ t.category + '.' + t.state  + '.' + t.value}}</el-descriptions-item>
              </el-descriptions>
            </el-card>
          </div>
          <!-- 添加按钮和配置对话框 -->
          <el-card style="display: flex;align-content: center;justify-content: center;" class="card-margin">
            <el-button type="text" @click="addState()">
              <i class="el-icon-circle-plus" style="font-size: 25px;"> Add State</i>
            </el-button>
            <el-dialog title="State" :visible.sync="stateDialog" @close="cancelAddState">
              <el-form ref="statesTemplate" :model="statesTemplate" label-width="20%" :rules="state_rules">
                <el-form-item label="Location" prop="location">
                  <el-select v-model="statesTemplate.location" placeholder="Please Choose the Location" @change="statesLocationChange">
                    <el-option v-for="item in locationOptions" :key="item.value" :label="item.label" :value="item.value">
                    </el-option>
                  </el-select>
                </el-form-item>
                <el-form-item label="category" prop="category">
                  <el-select v-model="statesTemplate.category" placeholder="Please Choose the Category" @change="statesCategoryChange">
                    <el-option label="device" value="device"></el-option>
                    <el-option label="state" value="state"></el-option>
                  </el-select>
                </el-form-item>
                <el-form-item label="state" prop="state">
                  <el-select v-if="statesTemplate.category=='device'" v-model="statesTemplate.state" style="width: 120px;margin-right:20px" placeholder="Please Choose the Device"
                    @change="statesChange">
                    <el-option v-for="item in devicesOptions" :key="item.value" :label="item.label" :value="item.value">
                    </el-option>
                  </el-select>
                  <el-select v-if="statesTemplate.category=='state'" v-model="statesTemplate.state" style="width: 120px;margin-right:20px" placeholder="Please Choose the State" @change="statesChange">
                    <el-option v-for="item in statesOptions" :key="item.value" :label="item.label" :value="item.value">
                    </el-option>
                  </el-select>
                  <el-select v-model="statesTemplate.value" placeholder="Please Choose the Value">
                    <el-option v-for="item in valuesOptions" :key="item.value" :label="item.label" :value="item.value">
                    </el-option>
                  </el-select>
                </el-form-item>
              </el-form>
              <div slot="footer">
                <el-button @click="cancelAddState">Cancel</el-button>
                <el-button type="primary" @click="confimAddState">Confirm</el-button>
              </div>
            </el-dialog>
          </el-card>
        </el-col>
      </el-row>
    </el-main>
  </el-container>
</template>

<script>
import axios from 'axios'
export default {
  name: 'StateState',
  components: {
  },
  data () {
    return {
      dialogVisible: false,
      stateDialog: false,
      state: {
        category: '',
        state: '',
        location: '',
        value: ''
      },
      states: [],
      statesTemplate: {
        category: '',
        state: '',
        location: '',
        value: ''
      },
      state_rules: {
        location: [
          { required: true, message: 'Please select the location.', trigger: 'blur' }
        ],
        category: [
          { required: true, message: 'please select the category.', trigger: 'blur' }
        ],
        state: [
          { required: true, message: 'Please select the state.', trigger: 'blur' }
        ]
      },
      radio: 'always',
      locationOptions: [],
      stateOptions: [],
      statesOptions: [],
      deviceOptions: [],
      devicesOptions: [],
      valueOptions: [],
      valuesOptions: [],
      index: '',
      updateFlag: false
    }
  },
  mounted () {
    this.getLocation()
    if (this.$route.params.update >= 0) {
      const temp = this.$route.params.value.split(' & ')
      temp[1] = temp[1].substring(2)
      temp[temp.length - 2] = temp[temp.length - 2].substring(0, temp[temp.length - 2].length - 2)
      const state = temp[0].split('.')
      this.radio = temp[temp.length - 1]
      this.state = {
        location: state[0],
        category: state[1],
        state: state[2],
        value: state[3]
      }
      this.stateLocationChange(state[0])
      this.stateChange(state[2])
      this.state = {
        location: state[0],
        category: state[1],
        state: state[2],
        value: state[3]
      }
      for (let i = 1; i < temp.length - 1; i++) {
        const item = temp[i].split('.')
        this.states.push({
          location: item[0],
          category: item[1],
          state: item[2],
          value: item[3]
        })
      }
    }
  },
  methods: {
    updateItem (Info, index) {
      if (Info && index >= 0) {
        const item = JSON.parse(JSON.stringify(Info))
        this.stateDialog = true
        this.statesTemplate = item
        this.statesChange(this.statesTemplate.state)
        this.inquiryInfor(this.statesTemplate.category, 'statesTemplate')
        this.index = index
        this.updateFlag = true
      }
    },
    removeItem (index) {
      this.states.splice(index, 1)
      this.refresh()
    },
    getLocation () {
      const that = this
      this.locationOptions = []
      axios.get('/api/room_list', {
        headers: {
          'Content-Type': 'application/text'
        }
      }).then(res => {
        for (let i = 0; i < res.data.length; i++) {
          that.locationOptions.push({
            value: res.data[i],
            label: res.data[i]
          })
        }
      }).catch(err => {
        console.log(err)
      })
    },
    Confirm () {
      this.$refs.state.validate((valid) => {
        if (valid) {
          if (this.states.length < 1) {
            this.$message.warning('Please fill in at least one state!')
            this.dialogVisible = false
            return
          }
          if (this.state.value === '' || this.state.value === null) {
            this.$message.warning('Please fill in the form correctly!')
            this.dialogVisible = false
            return
          }
          let value = this.state.location + '.' + this.state.category + '.' + this.state.state + '.' + this.state.value + ' & ' + '[ ' + this.states[0].location + '.' + this.states[0].category + '.' + this.states[0].state + '.' + this.states[0].value
          let tempList = this.states[0].location + '.' + this.states[0].state + '.' + this.states[0].value
          for (let i = 1; i < this.states.length; i++) {
            value = value + ' & ' + this.states[i].location + '.' + this.states[i].category + '.' + this.states[i].state + '.' + this.states[i].value
            tempList = tempList + ' , ' + this.states[i].location + '.' + this.states[i].state + '.' + this.states[i].value
          }
          value = value + ' ]' + ' & ' + this.radio
          let ltl = ''
          if (this.radio === 'always') {
            ltl = 'G(' + this.state.location + '.' + this.state.category + '.' + this.state.state + '.' + this.state.value + ' & ' + tempList + ')'
          } else {
            ltl = 'F(' + this.state.location + '.' + this.state.category + '.' + this.state.state + '.' + this.state.value + ' & ' + tempList + ')'
          }
          if (this.$route.params.update >= 0) {
            this.PROPERTY.property[this.$route.params.update].value = value
            this.PROPERTY.property[this.$route.params.update].description = '[ ' + tempList + ', ' + this.state.location + '.' + this.state.category + '.' + this.state.state + '.' + this.state.value + ' ] will [ ' + this.radio + ' ] happen simultaneously.'
            this.PROPERTY.property[this.$route.params.update].ltl = ltl
          } else {
            this.PROPERTY.property.push(
              {
                template: 'state-state',
                value: value,
                description: '[ ' + tempList + ', ' + this.state.location + '.' + this.state.category + '.' + this.state.state + '.' + this.state.value + ' ] will [ ' + this.radio + ' ] happen simultaneously.',
                ltl: ltl
              }
            )
          }
          this.dialogVisible = false
          this.$router.back()
        } else {
          this.$message.warning('Please fill in the form correctly!')
          this.dialogVisible = false
        }
      })
    },
    inquiryInfor (val, template) {
      const that = this
      if (template === 'state') {
        if (val === 'device') {
          this.deviceOptions = []
          axios.get('/api/room_device/' + that.state.location, {
            headers: {
              'Content-Type': 'application/text'
            }
          }).then(res => {
            for (let i = 0; i < res.data.length; i++) {
              const data = res.data[i].slice(0, -3)
              // var temp = data[0:-3]
              that.deviceOptions.push({
                value: data,
                label: data
              })
            }
          }).catch(err => {
            console.log(err)
          })
        } else if (val === 'state') {
          this.stateOptions = []
          axios.get('/api/room_state/' + that.state.location, {
            headers: {
              'Content-Type': 'application/text'
            }
          }).then(res => {
            for (let i = 0; i < res.data.length; i++) {
              that.stateOptions.push({
                value: res.data[i],
                label: res.data[i]
              })
            }
          }).catch(err => {
            console.log(err)
          })
        }
      } else if (template === 'statesTemplate') {
        const that = this
        if (this.statesTemplate.category === 'device') {
          this.devicesOptions = []
          axios.get('/api/room_device/' + that.statesTemplate.location, {
            headers: {
              'Content-Type': 'application/text'
            }
          }).then(res => {
            for (let i = 0; i < res.data.length; i++) {
              const data = res.data[i].slice(0, -3)
              // var temp = data[0:-3]
              that.devicesOptions.push({
                value: data,
                label: data
              })
            }
          }).catch(err => {
            console.log(err)
          })
        } else if (this.statesTemplate.category === 'state') {
          this.statesOptions = []
          axios.get('/api/room_state/' + that.statesTemplate.location, {
            headers: {
              'Content-Type': 'application/text'
            }
          }).then(res => {
            for (let i = 0; i < res.data.length; i++) {
              that.statesOptions.push({
                value: res.data[i],
                label: res.data[i]
              })
            }
          }).catch(err => {
            console.log(err)
          })
        }
      }
    },
    stateLocationChange (val) {
      this.state.state = null
      this.state.value = null
      this.inquiryInfor(this.state.category, 'state')
    },
    stateCategoryChange (val) {
      this.state.state = null
      this.state.value = null
      this.inquiryInfor(val, 'state')
    },
    statesLocationChange (val) {
      this.statesTemplate.state = null
      this.statesTemplate.value = null
      this.inquiryInfor(this.statesTemplate.category, 'statesTemplate')
    },
    statesCategoryChange (val) {
      this.statesTemplate.state = null
      this.statesTemplate.value = null
      this.inquiryInfor(val, 'statesTemplate')
    },
    stateChange (val) {
      this.state.value = null
      if (val) {
        this.valueOptions = this.STATE.state[val]
      } else {
        this.valueOptions = []
      }
    },
    statesChange (val) {
      this.statesTemplate.value = null
      if (this.statesTemplate.state) {
        this.valuesOptions = this.STATE.state[val]
      } else {
        this.valuesOptions = []
      }
    },
    addState () {
      this.stateDialog = true
      this.updateFlag = false
    },
    cancelAddState () {
      this.stateDialog = false
      this.refresh()
    },
    confimAddState () {
      this.$refs.statesTemplate.validate((valid) => {
        if (valid) {
          if (this.statesTemplate.value === '' || this.statesTemplate.value === null) {
            this.$message.warning('Please fill in the form correctly!')
            return
          }
          this.stateDialog = false
          if (this.updateFlag) {
            this.states[this.index] = this.statesTemplate
          } else {
            this.states.push(this.statesTemplate)
          }
          this.cancelAddState()
          this.statesTemplate = {
            category: '',
            state: '',
            value: ''
          }
        } else {
          this.$message.warning('Please fill in the form correctly!')
        }
      })
    },
    // 更新界面
    refresh () {
      this.$forceUpdate()
    }
  }
}
</script>

<style>
.state-state-container {
  height: 100%;
  width: 100%;
}

.home-title {
  margin-top: 10px;
  margin-bottom: 10px;
}

.card-margin {
  margin: 10px 20px;
}

.el-card__body {
  width: 90%;
  margin-top: 20px;
  margin: auto auto;
}
</style>
