<template>
  <el-dialog title="event-state-time" :visible.sync="EventStateTimeDialog" @close="cancelAddEventStateTime">
    <el-radio v-model="radio" label="always">always</el-radio>
    <el-radio v-model="radio" label="never">never</el-radio>
    <el-alert style="margin-top:10px;margin-bottom:20px;" v-if="radio == 'always'" title="The [State] always occur [within/after] the [Time] of the [Event] occurrence." type="success"
      :closable="false">
    </el-alert>
    <el-alert style="margin-top:10px;margin-bottom:20px;" v-if="radio == 'never'" title="The [State] never occur [within/after] the [Time] of the [Event] occurrence." type="success" :closable="false">
    </el-alert>
    <el-form ref="EventStateTime" :model="EventStateTime" label-width="20%" :rules="event_event_time_rule">
      <el-form-item label="Event Location" prop="location">
        <el-select v-model="EventStateTime.location" placeholder="Please Choose the Location of the Event" @change="eventLocationChange">
          <el-option v-for="item in locationOptions" :key="item.value" :label="item.label" :value="item.value">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="Event" prop="event">
        <el-select v-model="EventStateTime.type" placeholder="Please Choose the Type" @change="typeChange" style="width: 120px;margin-right:20px">
          <el-option v-for="item in typeOptions" :key="item.value" :label="item.label" :value="item.value">
          </el-option>
        </el-select>
        <el-select v-model="EventStateTime.event" placeholder="Please Choose the Event" v-if="EventStateTime.type == 'event'">
          <el-option v-for="item in eventOptions" :key="item.value" :label="item.label" :value="item.value">
          </el-option>
        </el-select>
        <el-select v-model="EventStateTime.event" placeholder="Please Choose the Action" v-if="EventStateTime.type == 'action'">
          <el-option v-for="item in actionOptions" :key="item.value" :label="item.label" :value="item.value">
          </el-option>
        </el-select>
      </el-form-item>

      <el-form-item label="State Location" prop="location_two">
        <el-select v-model="EventStateTime.location_two" placeholder="Please Choose the Location" @change="LocationChange">
          <el-option v-for="item in locationOptions" :key="item.value" :label="item.label" :value="item.value">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="Category" prop="category">
        <el-select v-model="EventStateTime.category" placeholder="Please Choose the Category" @change="stateCategoryChange">
          <el-option label="device" value="device"></el-option>
          <el-option label="state" value="state"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="State" prop="state">
        <el-select v-if="EventStateTime.category=='device'" v-model="EventStateTime.state" placeholder="Please Choose the Device" @change="stateChange" style="width: 120px;margin-right:20px">
          <el-option v-for="item in deviceOptions" :key="item.value" :label="item.label" :value="item.value">
          </el-option>
        </el-select>
        <el-select v-if="EventStateTime.category=='state'" v-model="EventStateTime.state" placeholder="Please Choose the State" @change="stateChange" style="width: 120px;margin-right:20px">
          <el-option v-for="item in stateOptions" :key="item.value" :label="item.label" :value="item.value">
          </el-option>
        </el-select>
        <el-select v-model="EventStateTime.value" placeholder="Please Choose the Value">
          <el-option v-for="item in valueOptions" :key="item.value" :label="item.label" :value="item.value">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="Interval" prop="interval">
        <span>
          <el-input-number v-model="EventStateTime.interval" controls-position="right" :min="1" style="width: 120px;margin-right:20px">
          </el-input-number>
        </span>
        <el-select v-model="EventStateTime.intervalUnit" placeholder="Please Choose the Minimum Interval" style="width: 240px">
          <el-option label="min" value="min"></el-option>
          <el-option label="hour" value="hour"></el-option>
          <el-option label="day" value="day"></el-option>
        </el-select>
      </el-form-item>
    </el-form>
    <div slot="footer">
      <el-button @click="cancelAddEventStateTime">Cancel</el-button>
      <el-button type="primary" @click="confirmAddEventStateTime">Confirm</el-button>
    </div>
  </el-dialog>
</template>

<script>
import { symbolOptions, typeOptions } from '../../public/global/data.js'
import axios from 'axios'
export default {
  methods: {
    eventLocationChange (val) {
      this.EventStateTime.event = null
      const that = this
      axios.get('/api/event_list/' + val, {
        headers: {
          'Content-Type': 'application/text'
        }
      }).then(res => {
        this.eventOptions = []
        for (let i = 0; i < res.data.length; i++) {
          that.eventOptions.push({
            value: res.data[i].slice(6, res.data[i].length),
            label: res.data[i].slice(6, res.data[i].length)
          })
        }
      }).catch(err => {
        console.log(err)
      })
      axios.get('/api/room_action/' + val, {
        headers: {
          'Content-Type': 'application/text'
        }
      }).then(res => {
        this.actionOptions = []
        for (let i = 0; i < res.data.length; i++) {
          that.actionOptions.push({
            value: res.data[i],
            label: res.data[i]
          })
        }
      }).catch(err => {
        console.log(err)
      })
    },
    typeChange (val) {
      this.EventStateTime.event = null
    },
    getLocation () {
      const that = this
      axios.get('/api/room_list', {
        headers: {
          'Content-Type': 'application/text'
        }
      }).then(res => {
        for (let i = 0; i < res.data.length; i++) {
          that.locationOptions.push({
            value: res.data[i],
            label: res.data[i]
          })
        }
      }).catch(err => {
        console.log(err)
      })
    },
    LocationChange (val) {
      this.EventStateTime.state = null
      this.EventStateTime.value = null
      this.inquiryInfor(this.EventStateTime.category)
    },
    stateCategoryChange (val) {
      this.EventStateTime.state = null
      this.EventStateTime.value = null
      this.inquiryInfor(val)
    },
    stateChange (val) {
      this.EventStateTime.value = null
      if (val) {
        this.valueOptions = this.STATE.state[val]
      } else {
        this.valueOptions = []
      }
    },
    inquiryInfor (val) {
      const that = this
      if (val === 'device') {
        this.deviceOptions = []
        axios.get('/api/room_device/' + that.EventStateTime.location_two, {
          headers: {
            'Content-Type': 'application/text'
          }
        }).then(res => {
          for (let i = 0; i < res.data.length; i++) {
            const data = res.data[i].slice(0, -3)
            // var temp = data[0:-3]
            that.deviceOptions.push({
              value: data,
              label: data
            })
          }
        }).catch(err => {
          console.log(err)
        })
      } else if (val === 'state') {
        this.stateOptions = []
        axios.get('/api/room_state/' + that.EventStateTime.location_two, {
          headers: {
            'Content-Type': 'application/text'
          }
        }).then(res => {
          for (let i = 0; i < res.data.length; i++) {
            that.stateOptions.push({
              value: res.data[i],
              label: res.data[i]
            })
          }
        }).catch(err => {
          console.log(err)
        })
      }
    },
    cancelAddEventStateTime () {
      this.EventStateTimeDialog = false
    },
    confirmAddEventStateTime () {
      const that = this
      this.$refs.EventStateTime.validate((valid) => {
        if (valid) {
          if (this.EventStateTime.intervalUnit === '' || this.EventStateTime.value === '' || this.EventStateTime.value === null) {
            this.$message.warning('Please fill in the form correctly!')
            return
          }
          this.EventStateTimeDialog = false
          const value = this.EventStateTime.location + '.' + this.EventStateTime.type + '.' + this.EventStateTime.event + ' & ' + this.EventStateTime.location_two + '.' + this.EventStateTime.category + '.' + this.EventStateTime.state + '.' + this.EventStateTime.value + ' & ' + this.EventStateTime.interval + this.EventStateTime.intervalUnit + ' & ' + this.radio
          if (this.updateFlag) {
            this.PROPERTY.property[this.index].value = value
            this.PROPERTY.property[this.index].description = 'After [' + this.EventStateTime.location + '.' + this.EventStateTime.event + '], within [ ' + this.EventStateTime.interval + this.EventStateTime.intervalUnit + ' ], [ ' + this.EventStateTime.location_two + '.' + this.EventStateTime.state + '.' + this.EventStateTime.value + ' ] will [ ' + this.radio + ' ] happen.'
          } else {
            this.PROPERTY.property.push(
              {
                template: 'event-state-time',
                value: value,
                description: 'After [' + this.EventStateTime.location + '.' + this.EventStateTime.event + '], within [ ' + this.EventStateTime.interval + this.EventStateTime.intervalUnit + ' ], [ ' + this.EventStateTime.location_two + '.' + this.EventStateTime.state + '.' + this.EventStateTime.value + ' ] will [ ' + this.radio + ' ] happen.'
              })
          }
          Object.keys(this.EventStateTime).forEach(function (key) {
            that.EventStateTime[key] = ''
          })
        } else {
          this.$message.warning('Please fill in the form correctly!')
        }
      })
    }
  },
  mounted () {
    this.getLocation()
  },
  data () {
    return {
      radio: 'always',
      index: -1,
      updateFlag: false,
      locationOptions: [],
      eventOptions: [],
      eventOptionsTwo: [],
      actionOptions: [],
      stateOptions: [],
      deviceOptions: [],
      valueOptions: [],
      actionOptionsTwo: [],
      typeOptions,
      symbolOptions,
      EventStateTimeDialog: false,
      EventStateTime: {
        location: '',
        type: '',
        event: '',
        location_two: '',
        category: '',
        state: '',
        value: '',
        interval: '',
        intervalUnit: ''
      },
      event_event_time_rule: {
        location: [
          { required: true, message: 'please select the event location.', trigger: 'blur' }
        ],
        event: [
          { required: true, message: 'please select the first event.', trigger: 'blur' }
        ],
        category: [
          { required: true, message: 'please select the category.', trigger: 'blur' }
        ],
        state: [
          { required: true, message: 'please select the state.', trigger: 'blur' }
        ],
        location_two: [
          { required: true, message: 'please select the state location.', trigger: 'blur' }
        ],
        interval: [
          { required: true, message: 'please select the min time interval between event and state.', trigger: 'blur' }
        ]
      }
    }
  }
}
</script>

