<template>
  <el-dialog title="Effect-Time" :visible.sync="EffectTimeDialog" @close="cancelAddEffectTime">
    <el-alert style="margin-bottom:20px;" title="'The execution time of the [Effect] must be [greater than/less than] [Time]." type="success" :closable="false">
    </el-alert>
    <el-form ref="EffectTime" :model="EffectTime" label-width="20%" :rules="effect_time_rule">
      <el-form-item label="Location" prop="location">
        <el-select v-model="EffectTime.location" placeholder="Please Choose the Location" @change="LocationChange">
          <el-option v-for="item in locationOptions" :key="item.value" :label="item.label" :value="item.value">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="Effect" prop="effect">
        <el-select v-model="EffectTime.effect" placeholder="Please Choose the Effect">
          <el-option v-for="item in effectsOptions" :key="item.value" :label="item.label" :value="item.value">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="Symbol" prop="symbol">
        <el-select v-model="EffectTime.symbol" placeholder="Please Choose the Symbol">
          <el-option v-for="item in symbolOptions" :key="item.value" :label="item.label" :value="item.value">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="Time" prop="time">
        <span>
          <el-input-number v-model="EffectTime.time" controls-position="right" :min="1" style="width: 120px;margin-right:20px">
          </el-input-number>
        </span>
        <el-select v-model="EffectTime.timeUnit" placeholder="Please Choose the Minimum/Maximum Execution Time of the Effect" style="width: 240px">
          <el-option label="min" value="min"></el-option>
          <el-option label="hour" value="hour"></el-option>
          <el-option label="day" value="day"></el-option>
        </el-select>
      </el-form-item>
    </el-form>
    <div slot="footer">
      <el-button @click="cancelAddEffectTime">Cancel</el-button>
      <el-button type="primary" @click="confirmAddEffectTime">Confirm</el-button>
    </div>
  </el-dialog>
</template>

<script>
import { symbolOptions } from '../../public/global/data.js'
import axios from 'axios'
export default {
  methods: {
    getLocation () {
      const that = this
      this.locationOptions = []
      axios.get('/api/room_list', {
        headers: {
          'Content-Type': 'application/text'
        }
      }).then(res => {
        for (let i = 0; i < res.data.length; i++) {
          that.locationOptions.push({
            value: res.data[i],
            label: res.data[i]
          })
        }
      }).catch(err => {
        console.log(err)
      })
    },
    cancelAddEffectTime () {
      this.EffectTimeDialog = false
    },
    confirmAddEffectTime () {
      const that = this
      this.$refs.EffectTime.validate((valid) => {
        if (valid) {
          if (this.EffectTime.timeUnit === '') {
            this.$message.warning('Please fill in the form correctly!')
            return
          }
          this.EffectTimeDialog = false
          const value = this.EffectTime.location + '.' + this.EffectTime.effect + ' & ' + this.EffectTime.symbol + ' & ' + this.EffectTime.time + this.EffectTime.timeUnit
          if (this.updateFlag) {
            this.PROPERTY.property[this.index].value = value
            this.PROPERTY.property[this.index].description = 'The duration of [ ' + this.EffectTime.location + '.' + this.EffectTime.effect + ' ] must [ ' + this.EffectTime.symbol + ' ' + this.EffectTime.time + this.EffectTime.timeUnit + ' ]'
          } else {
            this.PROPERTY.property.push({
              template: 'effect-time',
              value: value,
              description: 'The duration of [ ' + this.EffectTime.location + '.' + this.EffectTime.effect + ' ] must [ ' + this.EffectTime.symbol + ' ' + this.EffectTime.time + this.EffectTime.timeUnit + ' ]'
            })
          }
          Object.keys(this.EffectTime).forEach(function (key) {
            that.EffectTime[key] = ''
          })
        } else {
          this.$message.warning('Please fill in the form correctly!')
        }
      })
    },
    LocationChange (val) {
      this.EffectTime.effect = null
      this.effectsOptions = []
      const that = this
      axios.get('/api/effect_list/' + val, {
        headers: {
          'Content-Type': 'application/text'
        }
      }).then(res => {
        for (let i = 0; i < res.data.length; i++) {
          that.effectsOptions.push({
            value: res.data[i].slice(7, res.data[i].length),
            label: res.data[i].slice(7, res.data[i].length)
          })
        }
      }).catch(err => {
        console.log(err)
      })
    }
  },
  mounted () {
    this.getLocation()
  },
  data () {
    return {
      index: -1,
      updateFlag: false,
      EffectTimeDialog: false,
      symbolOptions,
      locationOptions: [],
      effectsOptions: [],
      EffectTime: {
        effect: '',
        location: '',
        symbol: '',
        time: '',
        timeUnit: ''
      },
      effect_time_rule: {
        effect: [
          { required: true, message: 'please select the effect.', trigger: 'blur' }
        ],
        location: [
          { required: true, message: 'please select the location.', trigger: 'blur' }
        ],
        symbol: [
          { required: true, message: 'please select the symbol.', trigger: 'blur' }
        ],
        time: [
          { required: true, message: 'please fill the execution time of effect.', trigger: 'blur' }
        ]
      }
    }
  }
}
</script>

