<template>
  <el-container class="effect-state-time-container">
    <el-main>
      <h1 class="home-title">State-Effect-Time</h1>
      <div style="display: flex;flex-direction: row-reverse;margin-top: 20px;">
        <el-button type="primary" @click="dialogVisible = true" style="margin-right: 30px;margin-bottom: 10px;">CONFIRM</el-button>
      </div>
      <el-radio v-model="radio" label="always">always</el-radio>
      <el-radio v-model="radio" label="never">never</el-radio>
      <el-alert style="margin-top:10px;margin-bottom:20px;" v-if="radio == 'always'" title="The [Effect] always happen [within/after] the [Time] while [state1, ..., staten]." type="success"
        :closable="false">
      </el-alert>
      <el-alert style="margin-top:10px;margin-bottom:20px;" v-if="radio == 'never'" title="The [Effect] never happen [within/after] the [Time] while [state1, ..., staten]." type="success"
        :closable="false">
      </el-alert>
      <el-dialog title="CONFIRM PROPERTY" :visible.sync="dialogVisible" center width="30%">
        <span>Please Confirm the Information</span>
        <span slot="footer">
          <el-button @click="dialogVisible = false">Cancel</el-button>
          <el-button type="primary" @click="Confirm">Confirm</el-button>
        </span>
      </el-dialog>
      <el-row :gutter="30">
        <el-col :span="12">
          <el-row :gutter="30">
            <el-card style="display: flex;align-content: center;justify-content: left;" class="card-margin">
              <el-form ref="effectTemplate" :model="effectTemplate" label-width="20%" :rules="effect_rules">
                <el-form-item label="Location" prop="location">
                  <el-select v-model="effectTemplate.location" placeholder="Please Choose the Location" @change="locationChange">
                    <el-option v-for="item in locationOptions" :key="item.value" :label="item.label" :value="item.value">
                    </el-option>
                  </el-select>
                </el-form-item>
                <el-form-item label="effect" prop="effect">
                  <el-select v-model="effectTemplate.effect" placeholder="Please Choose the Effect">
                    <el-option v-for="item in effectsOptions" :key="item.value" :label="item.label" :value="item.value">
                    </el-option>
                  </el-select>
                </el-form-item>
              </el-form>
            </el-card>
          </el-row>
          <el-row :gutter="30">
            <el-card style="display: flex;align-content: center;justify-content: left;" class="card-margin">
              <el-form ref="timeTemplate" :model="timeTemplate" label-width="20%" :rules="time_rules">
                <el-form-item label="Time" prop="time">
                  <span>
                    <el-input-number v-model="timeTemplate.time" controls-position="right" :min="1" style="width: 120px;margin-right:20px">
                    </el-input-number>
                  </span>
                </el-form-item>
                <el-form-item label="Time Unit" prop="timeUnit">
                  <el-select v-model="timeTemplate.timeUnit" placeholder="Please Choose the Minimum/Maximum Execution Time of the Effect" style="width: 240px">
                    <el-option label="min" value="min"></el-option>
                    <el-option label="hour" value="hour"></el-option>
                    <el-option label="day" value="day"></el-option>
                  </el-select>
                </el-form-item>
              </el-form>
            </el-card>
          </el-row>
        </el-col>
        <el-col :span="12">
          <!-- list 配置结果-->
          <div v-for="(t, i) in states" :key="i">
            <el-card class="card-margin">
              <el-descriptions :title="'State ' + (i + 1)" :column="1" border>
                <template slot="extra">
                  <el-button size="mini" icon="el-icon-edit" @click="updateItem(t, i)">
                  </el-button>
                  <el-button size="mini" icon="el-icon-delete" @click="removeItem(i)"></el-button>
                </template>
                <el-descriptions-item label="state">{{ t.location }}</el-descriptions-item>
                <el-descriptions-item label="state">{{ t.category + '.' + t.state  + '.' + t.value}}</el-descriptions-item>
              </el-descriptions>
            </el-card>
          </div>
          <!-- 添加按钮和配置对话框 -->
          <el-card style="display: flex;align-content: center;justify-content: center;" class="card-margin">
            <el-button type="text" @click="addState()">
              <i class="el-icon-circle-plus" style="font-size: 25px;"> Add State</i>
            </el-button>
            <el-dialog title="State" :visible.sync="stateDialog" @close="cancelAddState">
              <el-form ref="statesTemplate" :model="statesTemplate" label-width="20%" :rules="state_rules">
                <el-form-item label="Location" prop="location">
                  <el-select v-model="statesTemplate.location" placeholder="Please Choose the Location" @change="stateLocationChange">
                    <el-option v-for="item in locationOptions" :key="item.value" :label="item.label" :value="item.value">
                    </el-option>
                  </el-select>
                </el-form-item>
                <el-form-item label="category" prop="category">
                  <el-select v-model="statesTemplate.category" placeholder="Please Choose the Category  " @change="statesCategoryChange">
                    <el-option label="device" value="device"></el-option>
                    <el-option label="state" value="state"></el-option>
                  </el-select>
                </el-form-item>
                <el-form-item label="state" prop="state">
                  <el-select v-if="statesTemplate.category=='device'" v-model="statesTemplate.state" placeholder="Please Choose the Device" @change="statesChange"
                    style="width: 120px;margin-right:20px">
                    <el-option v-for="item in deviceOptions" :key="item.value" :label="item.label" :value="item.value">
                    </el-option>
                  </el-select>
                  <el-select v-if="statesTemplate.category=='state'" v-model="statesTemplate.state" placeholder="Please Choose the State" @change="statesChange" style="width: 120px;margin-right:20px">
                    <el-option v-for="item in stateOptions" :key="item.value" :label="item.label" :value="item.value">
                    </el-option>
                  </el-select>
                  <el-select v-model="statesTemplate.value" placeholder="Please Choose the Value">
                    <el-option v-for="item in valueOptions" :key="item.value" :label="item.label" :value="item.value">
                    </el-option>
                  </el-select>
                </el-form-item>
              </el-form>
              <div slot="footer">
                <el-button @click="cancelAddState">Cancel</el-button>
                <el-button type="primary" @click="confimAddState">Confirm</el-button>
              </div>
            </el-dialog>
          </el-card>
        </el-col>
      </el-row>
    </el-main>
  </el-container>
</template>

<script>
import axios from 'axios'
import { symbolOptions } from '../../public/global/data.js'
export default {
  name: 'StatetimeTemplate',
  components: {
  },
  data () {
    return {
      timeTemplate: {
        symbel: '',
        time: '',
        timeUnit: ''
      },
      dialogVisible: false,
      stateDialog: false,
      effectTemplate: {
        effect: '',
        location: ''
      },
      symbolOptions,
      states: [],
      statesTemplate: {
        category: '',
        state: '',
        value: '',
        location: ''
      },
      time_rules: {
        time: [
          { required: true, message: 'please fill the execution time of effect.', trigger: 'blur' }
        ],
        timeUnit: [
          { required: true, message: 'please fill the execution timeUnit of effect.', trigger: 'blur' }
        ]
      },
      state_rules: {
        location: [
          { required: true, message: 'please select the location.', trigger: 'blur' }
        ],
        category: [
          { required: true, message: 'please select the category.', trigger: 'blur' }
        ],
        state: [
          { required: true, message: 'Please select the state.', trigger: 'blur' }
        ]
      },
      effect_rules: {
        location: [
          { required: true, message: 'please select the location.', trigger: 'blur' }
        ],
        effect: [
          { required: true, message: 'please select the effect.', trigger: 'blur' }
        ]
      },
      radio: 'always',
      stateOptions: [],
      deviceOptions: [],
      effectsOptions: [],
      locationOptions: [],
      valueOptions: [],
      index: '',
      updateFlag: false
    }
  },
  mounted () {
    this.getLocation()
    if (this.$route.params.update >= 0) {
      const temp = this.$route.params.value.split(' & ')
      temp[1] = temp[1].substring(2)
      temp[temp.length - 3] = temp[temp.length - 3].substring(0, temp[temp.length - 3].length - 2)
      const effect = temp[0].split('.')
      this.radio = temp[temp.length - 2]
      this.locationChange(effect[0])
      this.effectTemplate = {
        location: effect[0],
        effect: effect[1]
      }
      this.timeTemplate = {
        time: parseInt(temp[temp.length - 1]),
        timeUnit: (/[^0-9]+/g.exec(temp[temp.length - 1]))[0]
      }
      for (let i = 1; i < temp.length - 2; i++) {
        const item = temp[i].split('.')
        this.states.push({
          location: item[0],
          category: item[1],
          state: item[2],
          value: item[3]
        })
      }
    }
  },
  methods: {
    updateItem (Info, index) {
      if (Info && index >= 0) {
        const item = JSON.parse(JSON.stringify(Info))
        this.stateDialog = true
        this.statesTemplate = item
        this.inquiryInfor(this.statesTemplate.category)
        this.statesChange(this.statesTemplate.state)
        this.index = index
        this.updateFlag = true
      }
    },
    removeItem (index) {
      this.states.splice(index, 1)
      this.refresh()
    },
    locationChange (val) {
      this.effectTemplate.effect = null
      this.effectsOptions = []
      const that = this
      axios.get('/api/effect_list/' + val, {
        headers: {
          'Content-Type': 'application/text'
        }
      }).then(res => {
        for (let i = 0; i < res.data.length; i++) {
          that.effectsOptions.push({
            value: res.data[i].slice(7, res.data[i].length),
            label: res.data[i].slice(7, res.data[i].length)
          })
        }
      }).catch(err => {
        console.log(err)
      })
    },
    stateLocationChange (val) {
      this.statesTemplate.state = null
      this.statesTemplate.value = null
      this.inquiryInfor(this.statesTemplate.category)
    },
    statesChange (val) {
      this.statesTemplate.value = null
      if (this.statesTemplate.state) {
        this.valueOptions = this.STATE.state[val]
      } else {
        this.valueOptions = []
      }
    },
    getLocation () {
      const that = this
      this.locationOptions = []
      axios.get('/api/room_list', {
        headers: {
          'Content-Type': 'application/text'
        }
      }).then(res => {
        for (let i = 0; i < res.data.length; i++) {
          that.locationOptions.push({
            value: res.data[i],
            label: res.data[i]
          })
        }
      }).catch(err => {
        console.log(err)
      })
    },
    Confirm () {
      this.$refs.effectTemplate.validate((effect) => {
        this.$refs.timeTemplate.validate((time) => {
          if (effect && time) {
            if (this.states.length < 1) {
              this.$message.warning('Please fill in at least one state!')
              this.dialogVisible = false
              return
            }
            let stateList = this.states[0].location + '.' + this.states[0].state + '.' + this.states[0].value
            let value = this.effectTemplate.location + '.' + this.effectTemplate.effect + ' & ' + '[ ' + this.states[0].location + '.' + this.states[0].category + '.' + this.states[0].state + '.' + this.states[0].value
            let descList = this.states[0].location + '.' + this.states[0].state + '.' + this.states[0].value
            for (let i = 1; i < this.states.length; i++) {
              value = value + ' & ' + this.states[i].location + '.' + this.states[i].category + '.' + this.states[i].state + '.' + this.states[i].value
              descList = descList + ' , ' + this.states[i].location + '.' + this.states[i].state + '.' + this.states[i].value
              stateList = stateList + ' & ' + this.states[i].location + '.' + this.states[i].state + '.' + this.states[i].value
            }
            value = value + ' ]' + ' & ' + this.radio + ' & ' + this.timeTemplate.time + this.timeTemplate.timeUnit
            if (this.$route.params.update >= 0) {
              this.PROPERTY.property[this.$route.params.update].value = value
              this.PROPERTY.property[this.$route.params.update].description = 'After [' + descList + '], within  [ ' + this.timeTemplate.time + this.timeTemplate.timeUnit + ' ], [ ' + this.effectTemplate.location + '.' + this.effectTemplate.effect + ' ] will [ ' + this.radio + ' ] happen.'
            } else {
              this.PROPERTY.property.push(
                {
                  template: 'effect-state-time',
                  value: value,
                  description: 'After [' + descList + '], within  [ ' + this.timeTemplate.time + this.timeTemplate.timeUnit + ' ], [ ' + this.effectTemplate.location + '.' + this.effectTemplate.effect + ' ] will [ ' + this.radio + ' ] happen.'
                }
              )
            }
            this.$router.back()
          } else {
            this.dialogVisible = false
            this.$message.warning('Please fill in the form correctly!')
          }
        })
      })
    },
    inquiryInfor (val) {
      const that = this
      if (val === 'device') {
        this.deviceOptions = []
        axios.get('/api/room_device/' + that.statesTemplate.location, {
          headers: {
            'Content-Type': 'application/text'
          }
        }).then(res => {
          for (let i = 0; i < res.data.length; i++) {
            const data = res.data[i].slice(0, -3)
            // var temp = data[0:-3]
            that.deviceOptions.push({
              value: data,
              label: data
            })
          }
        }).catch(err => {
          console.log(err)
        })
      } else if (val === 'state') {
        this.stateOptions = []
        axios.get('/api/room_state/' + that.statesTemplate.location, {
          headers: {
            'Content-Type': 'application/text'
          }
        }).then(res => {
          for (let i = 0; i < res.data.length; i++) {
            that.stateOptions.push({
              value: res.data[i],
              label: res.data[i]
            })
          }
        }).catch(err => {
          console.log(err)
        })
      }
    },
    statesCategoryChange (val) {
      this.statesTemplate.state = null
      this.statesTemplate.value = null
      this.inquiryInfor(val)
    },
    addState () {
      this.stateDialog = true
      this.updateFlag = false
    },
    cancelAddState () {
      this.stateDialog = false
      this.statesTemplate = {
        category: '',
        state: '',
        value: '',
        location: ''
      }
      this.refresh()
    },
    confimAddState () {
      this.$refs.statesTemplate.validate((valid) => {
        if (valid) {
          if (this.statesTemplate.value === '' || this.statesTemplate.value === null) {
            this.$message.warning('Please fill in the form correctly!')
            return
          }
          this.stateDialog = false
          if (this.updateFlag) {
            this.states[this.index] = this.statesTemplate
          } else {
            this.states.push(this.statesTemplate)
          }
          this.cancelAddState()
          this.statesTemplate = {
            category: '',
            state: '',
            value: '',
            location: ''
          }
        } else {
          this.$message.warning('Please fill in the form correctly!')
        }
      })
    },
    // 更新界面
    refresh () {
      this.$forceUpdate()
    }
  }
}
</script>

<style>
.effect-state-time-container {
  height: 100%;
  width: 100%;
}

.home-title {
  margin-top: 10px;
  margin-bottom: 10px;
}

.card-margin {
  margin: 10px 20px;
}

.el-card__body {
  width: 90%;
  margin-top: 20px;
  margin: auto auto;
}
</style>
