<template>
  <el-dialog title="State-Time" :visible.sync="StateTimeDialog" @close="cancelAddStateTime">
    <el-alert style="margin-top:10px;margin-bottom:20px;" title="The duration of the [State] must be [greater than/less than] [Time]." type="success" :closable="false">
    </el-alert>
    <el-form ref="StateTime" :model="StateTime" label-width="20%" :rules="state_time_rule">
      <el-form-item label="Location" prop="location">
        <el-select v-model="StateTime.location" placeholder="Please Choose the Location" @change="LocationChange">
          <el-option v-for="item in locationOptions" :key="item.value" :label="item.label" :value="item.value">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="Category" prop="category">
        <el-select v-model="StateTime.category" placeholder="Please Choose the Category" @change="stateCategoryChange">
          <el-option label="device" value="device"></el-option>
          <el-option label="state" value="state"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="State" prop="state">
        <el-select v-if="StateTime.category=='device'" v-model="StateTime.state" placeholder="Please Choose the Device" @change="stateChange" style="width: 120px;margin-right:20px">
          <el-option v-for="item in deviceOptions" :key="item.value" :label="item.label" :value="item.value">
          </el-option>
        </el-select>
        <el-select v-if="StateTime.category=='state'" v-model="StateTime.state" placeholder="Please Choose the State" @change="stateChange" style="width: 120px;margin-right:20px">
          <el-option v-for="item in stateOptions" :key="item.value" :label="item.label" :value="item.value">
          </el-option>
        </el-select>
        <el-select v-model="StateTime.value" placeholder="Please Choose the Value">
          <el-option v-for="item in valueOptions" :key="item.value" :label="item.label" :value="item.value">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="Symbol" prop="symbol">
        <el-select v-model="StateTime.symbol" placeholder="Please Choose the Symbol">
          <el-option v-for="item in symbolOptions" :key="item.value" :label="item.label" :value="item.value">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="Time" prop="time">
        <span>
          <el-input-number v-model="StateTime.time" controls-position="right" :min="1" style="width: 120px;margin-right:20px">
          </el-input-number>
        </span>
        <el-select v-model="StateTime.timeUnit" placeholder="Please Choose the Minimum/Maximum Duration of the State" style="width: 240px">
          <el-option label="min" value="min"></el-option>
          <el-option label="hour" value="hour"></el-option>
          <el-option label="day" value="day"></el-option>
        </el-select>
      </el-form-item>
    </el-form>
    <div slot="footer">
      <el-button @click="cancelAddStateTime">Cancel</el-button>
      <el-button type="primary" @click="confirmAddStateTime">Confirm</el-button>
    </div>
  </el-dialog>
</template>

<script>
import { symbolOptions } from '../../public/global/data.js'
import axios from 'axios'
export default {
  methods: {
    getLocation () {
      const that = this
      this.locationOptions = []
      axios.get('/api/room_list', {
        headers: {
          'Content-Type': 'application/text'
        }
      }).then(res => {
        for (let i = 0; i < res.data.length; i++) {
          that.locationOptions.push({
            value: res.data[i],
            label: res.data[i]
          })
        }
      }).catch(err => {
        console.log(err)
      })
    },
    cancelAddStateTime () {
      this.StateTimeDialog = false
    },
    confirmAddStateTime () {
      const that = this
      this.$refs.StateTime.validate((valid) => {
        if (valid) {
          if (this.StateTime.timeUnit === null || this.StateTime.timeUnit === '' || this.StateTime.value === '' || this.StateTime.value === null) {
            this.$message.warning('Please fill in the form correctly!')
            return
          }
          const value = this.StateTime.location + '.' + this.StateTime.category + '.' + this.StateTime.state + '.' + this.StateTime.value + ' & ' + this.StateTime.symbol + ' & ' + this.StateTime.time + this.StateTime.timeUnit
          if (this.updateFlag) {
            this.PROPERTY.property[this.index].value = value
            this.PROPERTY.property[this.index].description = 'The duration of [ ' + this.StateTime.location + '.' + this.StateTime.state + '.' + this.StateTime.value + ' ] must [ ' + this.StateTime.symbol + ' ' + this.StateTime.time + this.StateTime.timeUnit + ' ]'
          } else {
            this.PROPERTY.property.push(
              {
                template: 'one-state',
                value: value,
                description: 'The duration of [ ' + this.StateTime.location + '.' + this.StateTime.state + '.' + this.StateTime.value + ' ] must [ ' + this.StateTime.symbol + ' ' + this.StateTime.time + this.StateTime.timeUnit + ' ]'
              }
            )
          }
          Object.keys(this.StateTime).forEach(function (key) {
            that.StateTime[key] = ''
          })
          this.StateTimeDialog = false
        } else {
          this.$message.warning('Please fill in the form correctly!')
          this.StateTimeDialog = false
        }
      })
    },
    inquiryInfor (val) {
      const that = this
      if (val === 'device') {
        this.deviceOptions = []
        axios.get('/api/room_device/' + that.StateTime.location, {
          headers: {
            'Content-Type': 'application/text'
          }
        }).then(res => {
          for (let i = 0; i < res.data.length; i++) {
            const data = res.data[i].slice(0, -3)
            // var temp = data[0:-3]
            that.deviceOptions.push({
              value: data,
              label: data
            })
          }
        }).catch(err => {
          console.log(err)
        })
      } else if (val === 'state') {
        this.stateOptions = []
        axios.get('/api/room_state/' + that.StateTime.location, {
          headers: {
            'Content-Type': 'application/text'
          }
        }).then(res => {
          for (let i = 0; i < res.data.length; i++) {
            that.stateOptions.push({
              value: res.data[i],
              label: res.data[i]
            })
          }
        }).catch(err => {
          console.log(err)
        })
      }
    },
    LocationChange (val) {
      this.StateTime.state = null
      this.StateTime.value = null
      this.inquiryInfor(this.StateTime.category)
    },
    stateCategoryChange (val) {
      this.StateTime.state = null
      this.StateTime.value = null
      this.inquiryInfor(val)
    },
    stateChange (val) {
      this.StateTime.value = null
      if (val) {
        this.valueOptions = this.STATE.state[val]
      } else {
        this.valueOptions = []
      }
    }
  },
  mounted () {
    this.getLocation()
  },
  data () {
    return {
      index: -1,
      updateFlag: false,
      symbolOptions,
      StateTimeDialog: false,
      locationOptions: [],
      stateOptions: [],
      deviceOptions: [],
      valueOptions: [],
      StateTime: {
        location: '',
        category: '',
        state: '',
        value: '',
        symbol: '',
        time: '',
        timeUnit: ''
      },
      state_time_rule: {
        category: [
          { required: true, message: 'please select the category.', trigger: 'blur' }
        ],
        state: [
          { required: true, message: 'please select the state.', trigger: 'blur' }
        ],
        location: [
          { required: true, message: 'please select the location.', trigger: 'blur' }
        ],
        symbol: [
          { required: true, message: 'please select the symbol.', trigger: 'blur' }
        ],
        time: [
          { required: true, message: 'please fill the execution time of state.', trigger: 'blur' }
        ]
      }
    }
  }
}
</script>

