<template>
  <el-container class="effect-effect-container">
    <el-main>
      <h1 class="home-title">Effect-Effect</h1>
      <div style="display: flex;flex-direction: row-reverse;margin-top: 20px;">
        <el-button type="primary" @click="dialogVisible = true" style="margin-right: 30px;margin-bottom: 10px;">CONFIRM</el-button>
      </div>
      <el-radio v-model="radio" label="always">always</el-radio>
      <el-radio v-model="radio" label="never">never</el-radio>
      <el-alert style="margin-top:10px;margin-bottom:20px;" v-if="radio == 'always'" title="Multiple [effect] always occur at the same time." type="success" :closable="false">
      </el-alert>
      <el-alert style="margin-top:10px;margin-bottom:20px;" v-if="radio == 'never'" title="Multiple [effect] never occur at the same time." type="success" :closable="false">
      </el-alert>
      <el-dialog title="CONFIRM PROPERTY" :visible.sync="dialogVisible" center width="30%">
        <span>Please Confirm the Information</span>
        <span slot="footer">
          <el-button @click="dialogVisible = false">Cancel</el-button>
          <el-button type="primary" @click="Confirm">Confirm</el-button>
        </span>
      </el-dialog>
      <!-- list 配置结果-->
      <div v-for="(t, i) in effectList" :key="i">
        <el-card class="card-margin">
          <el-descriptions :title="'effect ' + (i + 1)" :column="1" border>
            <template slot="extra">
              <el-button size="mini" icon="el-icon-edit" @click="updateItem(t, i)">
              </el-button>
              <el-button size="mini" icon="el-icon-delete" @click="removeItem(i)"></el-button>
            </template>
            <el-descriptions-item label="location">{{ t.location }}</el-descriptions-item>
            <el-descriptions-item label="effect">{{ t.effect }}</el-descriptions-item>
          </el-descriptions>
        </el-card>
      </div>
      <!-- 添加按钮和配置对话框 -->
      <el-card style="display: flex;align-content: center;justify-content: center;" class="card-margin">
        <el-button type="text" @click="addItem()">
          <i class="el-icon-circle-plus" style="font-size: 25px;"> Add Effect</i>
        </el-button>
        <el-dialog title="Effect" :visible.sync="chooseDialog" @close="cancelAddItem">
          <el-form ref="effectTemplate" :model="effectTemplate" label-width="20%" :rules="rules">
            <el-form-item label="Location" prop="location">
              <el-select v-model="effectTemplate.location" placeholder="Please Choose the Location" @change="LocationChange">
                <el-option v-for="item in locationOptions" :key="item.value" :label="item.label" :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="effect" prop="effect">
              <el-select v-model="effectTemplate.effect" placeholder="Please Choose the Effect">
                <el-option v-for="item in effectsOptions" :key="item.value" :label="item.label" :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
          </el-form>
          <div slot="footer">
            <el-button @click="cancelAddItem">Cancel</el-button>
            <el-button type="primary" @click="confimAddItem">Confirm</el-button>
          </div>
        </el-dialog>
      </el-card>
    </el-main>
  </el-container>
</template>

<script>
import axios from 'axios'
export default {
  name: 'EffectEffect',
  components: {
  },
  data () {
    return {
      radio: 'always',
      dialogVisible: false,
      chooseDialog: false,
      rules: {
        effect: [
          { required: true, message: 'please select the effect.', trigger: 'blur' }
        ],
        location: [
          { required: true, message: 'please select the location.', trigger: 'blur' }
        ]
      },
      effectList: [],
      effectTemplate: {
        effect: '',
        location: ''
      },
      index: '',
      updateFlag: false,
      effectsOptions: [],
      locationOptions: []
    }
  },
  mounted () {
    this.getLocation()
    if (this.$route.params.update >= 0) {
      const temp = this.$route.params.value.split(' & ')
      this.radio = temp[2]
      for (let i = 0; i < temp.length - 1; i++) {
        const item = temp[i].split('.')
        this.effectList.push({
          location: item[0],
          effect: item[1]
        })
      }
    }
  },
  methods: {
    updateItem (Info, index) {
      if (Info && index >= 0) {
        const item = JSON.parse(JSON.stringify(Info))
        this.chooseDialog = true
        this.effectTemplate = item
        const that = this
        axios.get('/api/effect_list/' + this.effectTemplate.location, {
          headers: {
            'Content-Type': 'application/text'
          }
        }).then(res => {
          that.effectsOptions = []
          for (let i = 0; i < res.data.length; i++) {
            that.effectsOptions.push({
              value: res.data[i].slice(7, res.data[i].length),
              label: res.data[i].slice(7, res.data[i].length)
            })
          }
        }).catch(err => {
          console.log(err)
        })
        this.index = index
        this.updateFlag = true
      }
    },
    removeItem (index) {
      this.effectList.splice(index, 1)
      this.refresh()
    },
    LocationChange (val) {
      this.effectTemplate.effect = null
      const that = this
      axios.get('/api/effect_list/' + val, {
        headers: {
          'Content-Type': 'application/text'
        }
      }).then(res => {
        this.effectsOptions = []
        for (let i = 0; i < res.data.length; i++) {
          that.effectsOptions.push({
            value: res.data[i].slice(7, res.data[i].length),
            label: res.data[i].slice(7, res.data[i].length)
          })
        }
      }).catch(err => {
        console.log(err)
      })
    },
    getLocation () {
      const that = this
      this.locationOptions = []
      axios.get('/api/room_list', {
        headers: {
          'Content-Type': 'application/text'
        }
      }).then(res => {
        that.locationOptions = []
        for (let i = 0; i < res.data.length; i++) {
          that.locationOptions.push({
            value: res.data[i],
            label: res.data[i]
          })
        }
      }).catch(err => {
        console.log(err)
      })
    },
    Confirm () {
      if (this.effectList.length < 2) {
        this.$message.warning('Please select more than two effects!')
        this.dialogVisible = false
        return
      }
      let value = this.effectList[0].location + '.' + this.effectList[0].effect
      let tempList = this.effectList[0].location + '.' + this.effectList[0].effect
      for (let i = 1; i < this.effectList.length; i++) {
        value = value + ' & ' + this.effectList[i].location + '.' + this.effectList[i].effect
        tempList = tempList + ' ， ' + this.effectList[i].location + '.' + this.effectList[i].effect
      }
      value = value + ' & ' + this.radio
      const temp = this.ltlMethod.getLTL(this.effectList)
      temp.then((result) => {
        let ltl = result.substring(3, result.length)
        if (this.radio === 'always') {
          ltl = 'G(' + ltl + ')'
        } else {
          ltl = 'F(' + ltl + ')'
        }
        if (this.$route.params.update >= 0) {
          this.PROPERTY.property[this.$route.params.update].value = value
          this.PROPERTY.property[this.$route.params.update].description = '[ ' + tempList + ' ] are [ ' + this.radio + ' ] occur simultaneously.'
          this.PROPERTY.property[this.$route.params.update].ltl = ltl
        } else {
          this.PROPERTY.property.push(
            {
              template: 'effect-effect',
              value: value,
              description: '[ ' + tempList + ' ] are [ ' + this.radio + ' ] occur simultaneously.',
              ltl: ltl
            }
          )
        }
        this.$router.back()
      })
    },
    // 增加property
    addItem () {
      this.chooseDialog = true
      this.updateFlag = false
    },
    // 增加property弹窗取消
    cancelAddItem () {
      this.chooseDialog = false
      this.refresh()
    },
    confimAddItem () {
      // const that = this
      this.$refs.effectTemplate.validate((valid) => {
        if (valid) {
          this.chooseDialog = false
          if (this.updateFlag) {
            this.effectList[this.index] = this.effectTemplate
          } else {
            this.effectList.push(this.effectTemplate)
          }
          this.cancelAddItem()
          this.effectTemplate = {
            effect: '',
            location: ''
          }
        } else {
          this.$message.warning('Please fill in the form correctly!')
        }
      })
    },
    // 更新界面
    refresh () {
      this.$forceUpdate()
    }
  }
}
</script>

<style>
.home-title {
  margin-top: 10px;
  margin-bottom: 10px;
}

.effect-effect-container {
  height: 100%;
  width: 100%;
}

.card-margin {
  margin: 10px 20px;
}
</style>
